.myBloodIconFalse {
  color: red;
  width: 20px;
  height: 20px;
}

.myBloodIconTrue {
  color: green;
  width: 20px;
  height: 20px;
}

.wrapper {
  display: flex;
  gap: 8px;

  margin-left: 8px;

  @include mixins.mq(tablet) {
    gap: 45px;
    margin-left: 20px;
  }

  @include mixins.mq(desktop) {
    gap: 30px;
    margin-left: 50px;
  }
}

.title {
  margin-left: 10px;
  display: flex;
  align-items: center;
  width: 100%;
  padding-bottom: 8px;

  border-bottom: 1px solid var(--border-color);

  overflow: hidden;

  @include mixins.mq(tablet) {
    width: 240px;
    padding-bottom: 20px;
  }
}

.title_first {
    align-items: center;
    width: 320px;
    padding-bottom: 8px;
  
    border-bottom: 1px solid var(--border-color);
  
    overflow: hidden;
  
    @include mixins.mq(tablet) {
      padding-bottom: 20px;
    }

}

.weight {
  display: flex;
  align-items: center;

  width: 51px;
  padding-bottom: 8px;

  border-bottom: 1px solid var(--border-color);

  @include mixins.mq(tablet) {
    width: 105px;
    padding-bottom: 20px;

    justify-content: right;
  }
}

.kcal {
  display: flex;
  align-items: center;

  width: 70px;
  padding-bottom: 8px;

  border-bottom: 1px solid var(--border-color);

  @include mixins.mq(tablet) {
    width: 105px;
    padding-bottom: 20px;

    justify-content: right;
  }
}

.btnRemove {
  margin-left: 10px;

  border: none;
  outline: none;
  background: none;

  @include mixins.mq(tablet) {
    margin-left: 30px;
    margin-right: 40px;
  }
}
